import axios from "axios"
export default {
    namespaced: true,
    state: {
        pilih_kokab: []
    },
    mutations: {
        SET_KOKAB(state, kotakab) {
            state.pilih_kokab = kotakab
        }
    },
    actions: {
        async kokab({ commit,rootState }) {
            try {
                let response = await axios.get('/siakad/city/province_id=' + rootState.biodata.info_bio.province_id, {
                
                })
                
                commit('SET_KOKAB', response.data.data)
            } catch (error) {
                console.warn(error)
            }
        }
    },
    getters: {
        datakokab(state) {
            return state.pilih_kokab
        }
    }
}