import axios from "axios"
export default {
    namespaced: true,
    state: {
        info_bio: [],
        info_res: []
    },
    mutations: {
        SET_BIODATA(state, databio) {
            state.info_bio = databio
        },
        SET_RESIDENCE(state, dataresi) {
            state.info_res = dataresi
        }
    },
    actions: {
        async biodata({ commit }) {
            try {
                let response = await axios.get('/oasis/bio/npm=' + localStorage.getItem('userNpmTemp'), {
                    
                })
                commit('SET_BIODATA', response.data.data.bio),
                    commit('SET_RESIDENCE', response.data.data.residence)
            } catch (error) {
                console.warn(error)
            }
        }
    },
    getters: {
        databiodata(state) {
            return state.info_bio
        },
        dataresidence(state) {
            return state.info_res
        }
    }
}