import axios from "axios"
export default {
    namespaced: true,
    state: {
        info_profile: []
    },
    mutations: {
        SET_PROFILE(state, profile) {
            state.info_profile = profile
        }
    },
    getters: {
        profile(state) {
            return state.info_profile
        }
    },
    actions: {
        async profile({ commit }) {
            try{
                let response = await axios.get('/oasis/profile/npm=' + localStorage.getItem('userNpmTemp'), {
                    //
                })
                commit('SET_PROFILE', response.data.data)
            }catch(error){
                console.warn(error)
            }
            
        }
    }

}