import axios from 'axios'
export default {
    namespaced: true,
    state: {
        matkul: []
    },
    mutations: {
        SET_MATKUL(state, matkul) {
            state.matkul = matkul
        }
    },
    actions: {
        async matkul({ commit }) {
            try {
                let response = await axios.get('/oasis/guardianship-info/npm=' + localStorage.getItem('userNpmTemp'), {
                  
                })
                commit('SET_MATKUL', response.data.data)
            } catch (error) {
                console.warn(error)
            }
        }
    },
    getters: {
        matkul(state) {
            return state.matkul
        }
    }
}