import axios from 'axios'
export default {
    namespaced: true,
    state: {
        jenis_identitas: []
    },
    mutations: {
        SET_IDENTITAS(state, plhidentitas) {
            state.jenis_identitas = plhidentitas
        }
    },
    actions: {
        async identitas({ commit }) {
            try {
                let response = await axios.get('/siakad/identity-type', {
                    //
                })
            
                commit('SET_IDENTITAS', response.data.data)
            } catch (error) {
                console.warn(error)
            }
        }
    },
    getters: {
        pilihidentitas(state) {
            return state.jenis_identitas
        }
    }
}