import axios from "axios"
export default {
    namespaced: true,
    state: {
        info_father: [],
        info_mother: [],
        info_guardian: [],
        info_student: []
    },
    mutations: {
        SET_FATHER(state, datafat) {
            state.info_father = datafat
        },
        SET_MOTHER(state, datamot) {
            state.info_mother = datamot
        },
        SET_GUARDIAN(state, datagur) {
            state.info_guardian = datagur
        },
        SET_STUDENT(state, data) {
            state.info_student = data
        }
    },
    actions: {
        async ortu({ commit }) {
            try {
                let response = await axios.get('/oasis/parent/npm=' + localStorage.getItem('userNpmTemp'), {
                    //
                })
                // console.log(response.data.data)
                commit('SET_FATHER', response.data.data.father),
                    commit('SET_MOTHER', response.data.data.mother),
                    commit('SET_GUARDIAN', response.data.data.guardian),
                    commit('SET_STUDENT', response.data.data)
            } catch (error) {
                console.warn(error)
            }
        }
    },
    getters: {
        datafather(state) {
            return state.info_father
        },
        datamother(state) {
            return state.info_mother
        },
        dataguardian(state) {
            return state.info_guardian
        },
        datastudent(state) {
            return state.info_student
        }
    }
}