import axios from 'axios'
export default {
    namespaced: true,
    state: {
        info_kehadiran: [],
        info_kehadiran2: [],
        info_kehadiran3: [],
        info_kehadiran4: [],
        info_kehadiran5: [],
        info_kehadiran6: [],
        info_kehadiran7: [],
        info_kehadiran8: [],
    },
    mutations: {
        SET_1(state, kehadiran) {
            state.info_kehadiran = kehadiran
        },
        SET_2(state, kehadiran2) {
            state.info_kehadiran2 = kehadiran2
        },
        SET_3(state, kehadiran3) {
            state.info_kehadiran3 = kehadiran3
        },
        SET_4(state, kehadiran4) {
            state.info_kehadiran4 = kehadiran4
        },
        SET_5(state, kehadiran5) {
            state.info_kehadiran5 = kehadiran5
        },
        SET_6(state, kehadiran6) {
            state.info_kehadiran6 = kehadiran6
        },
        SET_7(state, kehadiran7) {
            state.info_kehadiran7 = kehadiran7
        },
        SET_8(state, kehadiran8) {
            state.info_kehadiran8 = kehadiran8
        },
    },
    actions: {
        async kehadiran({ commit }) {
            try {
                let response = await axios.get('/elearning/attendance-history/username=' + localStorage.getItem('userNpmTemp'), {
                    //
                })
                // console.log(response.data.data[0].semester1.courses)
                commit('SET_1', response.data.data[0].semester1.courses)
                commit('SET_2', response.data.data[1].semester2.courses)
                commit('SET_3', response.data.data[2].semester3.courses)
                commit('SET_4', response.data.data[3].semester4.courses)
                commit('SET_5', response.data.data[4].semester5.courses)
                commit('SET_6', response.data.data[5].semester6.courses)
                commit('SET_7', response.data.data[6].semester7.courses)
                commit('SET_8', response.data.data[7].semester8.courses)
            } catch (error) {
                console.warn(error)
            }
        }
    },
    getters: {
        mhshadir(state) {
            return state.info_kehadiran
        },
        mhshadir2(state) {
            return state.info_kehadiran2
        },
        mhshadir3(state) {
            return state.info_kehadiran3
        },
        mhshadir4(state) {
            return state.info_kehadiran4
        },
        mhshadir5(state) {
            return state.info_kehadiran5
        },
        mhshadir6(state) {
            return state.info_kehadiran6
        },
        mhshadir7(state) {
            return state.info_kehadiran7
        },
        mhshadir8(state) {
            return state.info_kehadiran8
        },
    }
}