import axios from 'axios'
export default {
    namespaced: true,
    state: {
        info_kp: []
    },
    mutations: {
        SET_KERJA(state, kp) {
            state.info_kp = kp
        }
    },
    actions: {
        async kp({ commit }) {
            try {
                let response = await axios.get('/siakad/get-practice/npm=' + localStorage.getItem('userNpmTemp'), {
                    //
                })
                // console.log(response.data.data.npm)
                commit('SET_KERJA', response.data.data)
            } catch (error) {
                console.warn(error)
            }
        }
    },
    getters: {
        getKP(state) {
            return state.info_kp
        }
    }
}