import axios from "axios"
export default {
    namespaced: true,
    state: {
        reset_password: []
    },
    mutations: {
        SET_RESET(state, resetpass) {
            state.reset_password = resetpass
        }
    },
    getters: {
        resetpassword(state) {
            return state.reset_password
        }
    },
    actions: {
        async passwordreset({ commit }) {
            try {
                let response = await axios.get("/password/find/" + "{masukan token}", {
                })
                commit('SET_RESET', response.data.data)
            } catch (error) {
                console.warn(error)
            }
        }
    }
}