import axios from 'axios'
export default {
    namespaced: true,
    state: {
        info_kuri: '',
        info_kuri2: [],
        info_kuri3: [],
        info_kuri4: [],
        info_kuri5: [],
        info_kuri6: [],
        info_kuri7: [],
        info_kuri8: [],
    },
    mutations: {
        SET_1(state, kurikulum) {
            state.info_kuri = kurikulum
        },
        SET_2(state, kurikulum2) {
            state.info_kuri2 = kurikulum2
        },
        SET_3(state, kurikulum3) {
            state.info_kuri3 = kurikulum3
        },
        SET_4(state, kurikulum4) {
            state.info_kuri4 = kurikulum4
        },
        SET_5(state, kurikulum5) {
            state.info_kuri5 = kurikulum5
        },
        SET_6(state, kurikulum6) {
            state.info_kuri6 = kurikulum6
        },
        SET_7(state, kurikulum7) {
            state.info_kuri7 = kurikulum7
        },
        SET_8(state, kurikulum8) {
            state.info_kuri8 = kurikulum8
        },
    },

    actions: {
        async kurikulum({ commit }) {
            try {
                let response = await axios.get('/siakad/my-curriculum/npm=' + localStorage.getItem('userNpmTemp'), {
                    //
                })
                console.log(response.data.data[0].semester1.courses)
                commit('SET_1', response.data.data[0].semester1)
                commit('SET_2', response.data.data[1].semester2)
                commit('SET_3', response.data.data[2].semester3)
                commit('SET_4', response.data.data[3].semester4)
                commit('SET_5', response.data.data[4].semester5)
                commit('SET_6', response.data.data[5].semester6)
                commit('SET_7', response.data.data[6].semester7)
                commit('SET_8', response.data.data[7].semester8)
            } catch (error) {
                console.log("Tak Ada Data")
            }

        }
    },
    getters: {
        kurikulum(state) {
            return state.info_kuri
        },
        kurikulum2(state) {
            return state.info_kuri2
        },
        kurikulum3(state) {
            return state.info_kuri3
        },
        kurikulum4(state) {
            return state.info_kuri4
        },
        kurikulum5(state) {
            return state.info_kuri5
        },
        kurikulum6(state) {
            return state.info_kuri6
        },
        kurikulum7(state) {
            return state.info_kuri7
        },
        kurikulum8(state) {
            return state.info_kuri8
        },
    }
}