import axios from "axios"
export default {
    namespaced: true,
    state: {
        all_kuliah: []
    },
    mutations: {
        SET_ALLJADWAL(state, kuliah) {
            state.all_kuliah = kuliah
        }
    },
    actions: {
        async allkuliah({ commit }) {
            try {
                let response = await axios.get('/siakad/get-class-schedule/npm=' + localStorage.getItem('userNpmTemp') + '&day=all', {
                   //
                })

                commit('SET_ALLJADWAL', response.data.data.list)
            } catch (error) {
                commit('SET_ALLJADWAL', "")
            }
        }
    },
    getters: {
        allkuliah(state) {
            return state.all_kuliah
        }
    }
}