<script>
import { mapActions, mapGetters } from "vuex";

export default {
  name: "Dashboard",
  data() {
    return {
      ver: null,
      schedule: [],
      news: [],
      firstName: "",
    };
  },
  computed: {
    ...mapGetters({
      getDataProfile: "profile/profile",
      getDataSupport: "it_support/msP_hsP",
      getDataJadwal: "kuliah/kuliah",
      getDataPengumuman: "pengumuman/pengumuman",
      getDataIP: "ipk/ipk",
    }),
    
  },
  methods: {
    ...mapActions({
      profile: "profile/profile",
      support: "it_support/support",
      jadwal: "kuliah/kuliah",
      pengumuman: "pengumuman/pengumuman",
      ipks: "ipk/ipk",
    }),
    number: function (index) {
      return index + 1;
    },
    getProfile() {
      this.profile();
    },
    getSupport() {
      this.support();
    },
    getJadwal() {
      this.jadwal();
    },
    getTable() {},
    getPengumuman() {
      this.pengumuman();
    },
    getIp() {
      this.ipks();
    },
  },

  mounted() {
    this.getProfile();
    this.getSupport();
    this.getJadwal();
    this.getPengumuman();
    this.getIp();
  },
};
</script>

