import axios from "axios"

export default {
    namespaced: true,
    state: {
        info_ip: []
    },
    mutations: {
        SET_IP(state, ipk) {
            state.info_ip = ipk
        }
    },
    actions: {
        async ipk({ commit }) {
            try {
                let response = await axios.get('/oasis/ipk/npm=' + localStorage.getItem('userNpmTemp'), {
                   //
                })

                commit('SET_IP', response.data.data)
            } catch (error) {
                commit('SET_IP',null)
            }
        }
    },
    getters: {
        ipk(state) {
            return state.info_ip
        }
    }
}