import axios from "axios";
export default {
  namespaced: true,
  state: {
    p_surat: [],
  },
  mutations: {
    SET_P_SURAT(state, datasurat) {
      state.p_surat = datasurat;
    },
  },
  getters: {
    p_surat_get(state) {
      return state.p_surat;
    },
  },
  actions: {
    async p_surat({ commit }) {
      try {
        let response = await axios.get("/siakad/get-letter-category", {
          
        });
        commit("SET_P_SURAT", response.data.data);
      } catch (error) {
        console.warn(error);
      }
    },
  },
};
