import axios from "axios"
export default {
    namespaced: true,
    state: {
        pilih_des   : []
    },
    mutations: {
        SET_DES(state, des) {
            state.pilih_des = des
        }
    },
    actions: {
        async desakelurahan({ commit,rootState }) {
            try {
                let response = await axios.get('/siakad/village/province_id=' + rootState.biodata.info_res.province_id + '&city_id='+rootState.biodata.info_res.city_id + '&district_id='+rootState.biodata.info_res.district_id, {
                
                })
                commit('SET_DES', response.data.data)
            } catch (error) {
                console.warn(error)
            }
        }
    },
    getters: {
        datades(state) {
            return state.pilih_des
        }
    }
}