import axios from 'axios'
export default {
    namespaced: true,
    state: {
        tempat_tinggal: []
    },
    mutations: {
        SET_TINGGAL(state, temting) {
            state.tempat_tinggal = temting
        }
    },
    actions: {
        async tinggal({ commit }) {
            try {
                let response = await axios.get('/siakad/residence-category', {
                    //
                })
                // console.log(response.data.data)
                commit('SET_TINGGAL', response.data.data)
            } catch (error) {
                console.warn(error)
            }
        }
    },
    getters: {
        datatinggal(state) {
            return state.tempat_tinggal
        }
    }
}