import axios from "axios"
export default {
    namespaced: true,
    state: {
        info_dospem: []
    },
    mutations: {
        SET_DOSENPEMBIMBING(state, dospm) {
            state.info_dospem = dospm
        }
    },
    actions: {
        async dospem({ commit }) {
            try {
                let response = await axios.get('/siakad/get-guide-practice/npm=' + localStorage.getItem('userNpmTemp'), {
                    //
                })
                // console.log(response.data.data[0])
                commit('SET_DOSENPEMBIMBING', response.data.data[0])
            } catch (error) {
                console.warn(error)
            }
        }
    },
    getters: {
        datadospem(state) {
            return state.info_dospem
        }
    }
}