import axios from "axios"
export default {
    namespaced: true,
    state: {
        find_email: []
    },
    mutations: {
        SET_FIND(state, emailnpm) {
            state.find_email = emailnpm
        }
    },
    getters: {
        showemail(state) {
            return state.find_email
        }
    },
    actions: {
        async showfind({ commit },npm) {
            try {
                let response = await axios.get("/password/find-username/username=" + npm, {
                    //
                })
                console.log(npm)
                commit('SET_FIND', response.data.data)
                return response
            } catch (error) {
                commit('SET_FIND', null)
            }
        }
    }
}