import axios from 'axios'
export default {
    namespaced: true,
    state: {
        pilih_agama: []
    },
    mutations: {
        SET_AGAMA(state, plhagama) {
            state.pilih_agama = plhagama
        }
    },
    actions: {
        async agama({ commit }) {
            try {
                let response = await axios.get('/siakad/religion', {
                   //
                })
                // console.log(response.data.data)
                commit('SET_AGAMA', response.data.data)
            } catch (error) {
                console.warn(error)
            }
        }
    },
    getters: {
        dataagama(state) {
            return state.pilih_agama
        }
    }
}