import axios from "axios"
export default {
    namespaced: true,
    state: {
        daftar_skripsi: ''
    },
    getters: {
        getDataSkripsi(state) {
            return state.daftar_skripsi
        }
    },
    mutations: {
        SET_DAFTARSKRIP(state, skripsiii) {
            state.daftar_skripsi = skripsiii
        }
    },
    actions: {
        async skripsi({ commit }) {
            try {
                let response = await axios.get('/siakad/get-thesis/username=' + localStorage.getItem('userNpmTemp'), {

                })
                // console.log(response.data.data)
                commit('SET_DAFTARSKRIP', response.data.data)
            } catch (error) {
                console.warn(error)
            }
        }
    }
}