import axios from "axios"
export default {
    namespaced: true,
    state: {
        status_kp: '',
        status_skripsi: '',
        status_porposal: '',
        status_terbuka: '',
        status_sidang_akhir: '',
        status_yudisium: ''
    },
    getters: {
        getStatus(state) {
            return state.status_kp
        },
        getStatusSkripsi(state) {
            return state.status_skripsi
        },
        getStatusProposal(state) {
            return state.status_porposal
        },
        getStatusTerbuka(state) {
            return state.status_terbuka
        },
        getStatusSidangAkhir(state) {
            return state.status_sidang_akhir
        },
        getStatusYudisium(state) {
            return state.status_yudisium
        }
    },
    mutations: {
        SET_STATUSKP(state, kp) {
            state.status_kp = kp
        },
        SET_THESIS(state, skripsi) {
            state.status_skripsi = skripsi
        },
        SET_PROPOSAL(state, proposal) {
            state.status_porposal = proposal
        },
        SET_TERBUKA(state, terbuka) {
            state.status_terbuka = terbuka
        },
        SET_SIDANG_AKHIR(state, sidangakhir) {
            state.status_porposal = sidangakhir
        },
        SET_YUDISIUM(state, yudisium) {
            state.status_yudisium = yudisium
        }
    },
    actions: {
        async getReport({ dispatch }) {
            try {
                let response = await axios.get('/siakad/report-check/npm=' + localStorage.getItem('userNpmTemp'), {

                })
                console.log(response.data.data.open_seminar_id)
                return dispatch('setReport', response.data.data)
            }
            catch (error) {
                console.warn(error)
            }
        },
        async setReport({ commit }, status) {
            commit('SET_STATUSKP', status.practice_id)
            commit('SET_THESIS', status.thesis_id)
            commit('SET_PROPOSAL', status.proposal_seminar_id)
            commit('SET_TERBUKA', status.open_seminar_id)
            commit('SET_SIDANG_AKHIR', status.thesis_defence_id)
            commit('SET_YUDISIUM', status.judiciaries_id)
        }
    }
}