import axios from 'axios'
export default {
    namespaced: true,
    state: {
        info_skkm_detail: []
    },
    mutations: {
        SET_SKKM_DETAIL(state, skkm) {
            state.info_skkm_detail = skkm
        }
    },
    actions: {
        async skkm_detail({ commit }, skkm_id) {
            try {
                let response = await axios.get('/kemahasiswaan/skkm-info-detail/npm=' + skkm_id, {
                    //
                })

                commit('SET_SKKM_DETAIL', response.data.data)
            } catch (error) {
                console.warn(error)
            }
        }
    },
    getters: {
        skkm_detail(state) {
            return state.info_skkm_detail
        }
    }
}