import axios from 'axios'
export default {
    namespaced: true,
    state: {
        info_kalender: ""
        
    },
    mutations: {
        SET_KALENDER(state, kalender) {
            state.info_kalender = kalender
        }
    },
    getters: {
        getCalendar(state) {
            return state.info_kalender
        }
    },
    actions: {
        async setCalendar({dispatch }) {
            try {
                let response = await axios.get('/siakad/calendar-academics', {
                    //
                })
                return dispatch("setStateCalendar", response.data.data.event)
            } catch (error) {
                console.warn(error)
            }
        },
        setStateCalendar({commit},event){
            commit("SET_KALENDER",event)
        }
    }
}