import axios from "axios"
export default {
    namespaced: true,
    state: {
        ubah_pass: []
    },
    getters: {
        ubahPassword(state) {
            return state.ubah_pass
        }
    },
    mutations: {
        SET_PASSWORD(state, ubahpass) {
            state.ubah_pass = ubahpass
        }
    },
    actions: {
        async ubahPassword({ commit }) {
            try {
                let response = await axios.get('/oasis/change-password', {
                })
                commit('SET_PASSWORD', response.data.data)
            } catch (error) {
                console.warn(error)
            }
        }
    },
}