import axios from "axios";
export default {
  namespaced: true,
  state: {
    riwayat_surat_limit: [],
  },
  getters: {

    getRiwayatLimit(state) {
      return state.riwayat_surat_limit;
    }
  },
  mutations: {

    SET_RIWAYATSUR_LIMIT(state, riwayatsurat) {
      state.riwayat_surat_limit = riwayatsurat
    }
  },
  actions: {

    async riwayatsuratjenislimit({ commit }) {
      try {
        let response = await axios.get(
          "/siakad/letter/username=" +
          localStorage.getItem("userNpmTemp") +
          "&all=false" + "&limit=5",
          {


          }
        );
        commit("SET_RIWAYATSUR_LIMIT", response.data.data[0]);
      } catch (error) {
        console.warn(error)
      }
    }

  },
};
