import axios from "axios"
export default {
    namespaced: true,
    state: {
        jenis_pekerjaan: []
    },
    mutations: {
        SET_PEKERJAAN(state, kerja) {
            state.jenis_pekerjaan = kerja
        }
    },
    actions: {
        async pekerjaan({ commit }) {
            try {
                let response = await axios.get('/siakad/job-field', {
                    //
                })
                commit('SET_PEKERJAAN', response.data.data)
            } catch (error) {
                console.warn(error)
            }
        }
    },
    getters: {
        datapekerjaan(state) {
            return state.jenis_pekerjaan
        }
    }
}