import axios from 'axios'
export default {
    namespaced: true,
    state: {
        info_skkm: []
    },
    mutations: {
        SET_SKKM(state, skkm) {
            state.info_skkm = skkm
        }
    },
    actions: {
        async skkm({ commit }) {
            try {
                let response = await axios.get('/kemahasiswaan/skkm-info/npm=' + localStorage.getItem('userNpmTemp'), {
                    //
                })
                commit('SET_SKKM', response.data.data)
            } catch (error) {
                console.warn(error)
            }
        }
    },
    getters: {
        skkm(state) {
            return state.info_skkm
        }
    }
}