import axios from "axios";
export default {
  namespaced: true,
  state: {
    asal_pendidikan: [],
  },
  mutations: {
    SET_PENDIDIK(state, pendidik) {
      state.asal_pendidikan = pendidik;
    },
  },
  actions: {
    async pendidikan({ commit }) {
      try {
        let response = await axios.get(
          "/oasis/education/npm=" + localStorage.getItem("userNpmTemp"),
          {

          }
        );
        // console.log(response.data.data)
        commit("SET_PENDIDIK", response.data.data);
      } catch (error) {
        console.warn(error);
      }
    },
  },
  getters: {
    datapendidikan(state) {
      return state.asal_pendidikan;
    },
  },
};
