import axios from 'axios'
export default {
    namespaced: true,
    state: {
        download_kehadiran: []
    },
    mutations: {
        SET_DOWNLOAD(state, attendance) {
            state.download_kehadiran = attendance
        }
    },
    actions: {
        async downloadkehadiran({ commit }) {
            try {
                let response = await axios.get('/elearning/attendance-history-download/npm=' + localStorage.getItem('userNpmTemp') + '&schedule_id=4', {
                    //
                })
                // console.log(response.data.data.history_attendance)
                commit('SET_DOWNLOAD', response.data.data)
            } catch (error) {
                console.warn(error)
            }
        }
    },
    getters: {
        datadownload(state) {
            return state.download_kehadiran
        }
    }
}