import axios from "axios";
export default {
  namespaced: true,
  state: {
    perwalian: [],
   
  },
  getters: {
    getPerwalian(state) {
      return state.perwalian;
    },
  
  },
  mutations: {
    SET_PERWALIAN(state, dataP) {
      state.perwalian = dataP;
    },
    
  },
  actions: {
    async perwalian({ commit }) {
      try {
        let response = await axios.get(
          "/oasis/preguardianship/npm=" + localStorage.getItem("userNpmTemp"),
          {}
        );
        commit("SET_PERWALIAN", response.data.data);
        
      } catch (error) {
        commit("SET_PERWALIAN", null)
      }
    },
  },
};
