import axios from "axios"
import store from "../store"
export default {
  namespaced: true,
  state: {
    token: null,
    users: null,
  },
  getters: {
    authenticated(state) {
      return state.token && state.users;
    },
    users(state) {
      return state.users;
    },
  },
  mutations: {
    SET_TOKEN(state, token) {
      state.token = token;
    },
    SET_USERS(state, username) {
      state.users = username;
    },
  },
  actions: {
    async signIn({ dispatch }, credentials) {
      let response = await axios.post("/oasis/login", credentials);
      return dispatch("attempt", response.data.data.token, localStorage.setItem('userNpmTemp', response.data.data.username))
    }
    ,
    async attempt({ commit, state }, token) {
      if (token) {
        commit("SET_TOKEN", token);
      }
      if (!state.token) {
        return;
      }
      try {
        let response = await axios.get(
          "/oasis/user/username=" + localStorage.getItem("userNpmTemp"),
          {
            headers: {
              Authorization: "Bearer " + token,
            },
          }
        );

        commit("SET_USERS", response.data.data[0]);
      } catch (e) {
        commit("SET_TOKEN", null);
        commit("SET_USERS", null);
      }
    },
    async signOut({ commit }) {
      return await axios.post( "/oasis/logout").then(() => {
        commit('SET_TOKEN', null)
        commit('SET_USERS', null)
        
        localStorage.removeItem('userNpmTemp')
        
      })
    }
  }

};
