import axios from 'axios'
export default {
    namespaced: true,
    state: {
        pilih_status: []
    },
    mutations: {
        SET_STATUS(state, status) {
            state.pilih_status = status
        }
    },
    actions: {
        async statusnikah({ commit }) {
            try {
                let response = await axios.get('/siakad/marital-status', {
                    //
                })
                // console.log(response.data.data)
                commit('SET_STATUS', response.data.data)
            } catch (error) {
                console.warn(error)
            }
        }
    },
    getters: {
        datastatus(state) {
            return state.pilih_status
        }
    }
}