import axios from 'axios'

export default {
    namespaced: true,
    state: {
        info_support: []
    },
    actions: {
        async support({ commit }) {
            try {
                let response = await axios.get('oasis/it-support/username=' + localStorage.getItem('userNpmTemp'), {
                    //
                })
                commit('SET_SUPPORT', response.data.data)
            } catch (error) {
                console.warn(error)
            }
        }
    },
    mutations: {
        SET_SUPPORT(state, support) {
            state.info_support = support
        }
    },
    getters: {
        msP_hsP(state) {
            return state.info_support
        }
    }
}