import axios from "axios"
export default {
    namespaced: true,
    state: {
        pilih_kec   : []
    },
    mutations: {
        SET_KEC(state, kec) {
            state.pilih_kec = kec
        }
    },
    actions: {
        async kecamatan({ commit,rootState }) {
            try {
                let response = await axios.get('/siakad/district/province_id=' + rootState.biodata.info_res.province_id + '&city_id='+rootState.biodata.info_res.city_id, {
                
                })
                commit('SET_KEC', response.data.data)
            } catch (error) {
                console.warn(error)
            }
        }
    },
    getters: {
        datakec(state) {
            return state.pilih_kec
        }
    }
}