import axios from "axios"
export default {
    namespaced: true,
    state: {
        provinsi: []
    },
    mutations: {
        SET_PROVINSI(state, databio) {
            state.provinsi = databio
        }
    },
    actions: {
        async provinsi({ commit }) {
            try {
                let response = await axios.get('/siakad/province', {
                    //
                })
                // console.log(response.data.data)
                commit('SET_PROVINSI', response.data.data)
            } catch (error) {
                console.warn(error)
            }
        }
    },
    getters: {
        dataprovinsi(state) {
            return state.provinsi
        }
    }
}