import axios from "axios"
export default {
    namespaced: true,
    state: {
        jenis_pendidikan: []
    },
    mutations: {
        SET_PENDIDIKAN(state, jnspnddk) {
            state.jenis_pendidikan = jnspnddk
        }
    },
    actions: {
        async pendidikan({ commit }) {
            try {
                let response = await axios.get('/siakad/education-level', {
                    
                })

                commit('SET_PENDIDIKAN', response.data.data)
            } catch (error) {
                console.warn(error)
            }
        }
    },
    getters: {
        datapendidikan(state) {
            return state.jenis_pendidikan
        }
    }
}