import axios from "axios";
export default {
  namespaced: true,
  state: {
    riwayat_surat: [],
  },
  getters: {
    getRiwayat(state) {
      return state.riwayat_surat;
    }
  },
  mutations: {
    SET_RIWAYATSUR(state, riwayatsurat) {
      state.riwayat_surat = riwayatsurat;
    }
  },
  actions: {
    async riwayatsuratjenis({ commit }) {
      try {
        let response = await axios.get(
          "/siakad/letter/username=" +
            localStorage.getItem("userNpmTemp") +
            "&all=true&limit=0",
          
        );
        commit("SET_RIWAYATSUR", response.data.data[0]);
      } catch (error) {
        console.warn(error);
      }
    },
    
    
   
  },
  
};
