import axios from "axios"
export default {
    namespaced: true,
    state: {
        perwalian3: []
    },
    mutations: {
        SET_STEP3(state, step3) {
            state.perwalian3 = step3
        }
    },
    getters: {
        step3(state) {
            return state.perwalian3
        }
    },
    actions: {
        async step3({ commit }) {
            try {
                let response = await axios.get('/oasis/guardianship-profile/npm=' + localStorage.getItem('userNpmTemp'), {
                   //
                })

                commit('SET_STEP3', response.data.data)
            } catch (error) {
                console.warn(error)
            }
        }
    }
}