import axios from "axios"
export default {
    namespaced: true,
    state: {
        info_dospem: '',
        info_dospem2: ''
    },
    getters: {
        getDospem(state) {
            return state.info_dospem
        },
        getDospem2(state) {
            return state.info_dospem2
        }
    },
    mutations: {
        SET_DOSPEM(state, skripsi) {
            state.info_dospem = skripsi
        },
        SET_DOSPEM2(state, skripsi2) {
            state.info_dospem2 = skripsi2
        }
    },
    actions: {
        async dospemskripsi({ commit }) {
            try {
                let response = await axios.get('/siakad/get-thesis-guide/username=' + localStorage.getItem('userNpmTemp'))
                // console.log(response.data.data[1])
                commit('SET_DOSPEM', response.data.data[0]),
                    commit('SET_DOSPEM2', response.data.data[1])
            } catch (error) {
                console.warn(error)
            }
        }
    }
}