import axios from 'axios'
export default {
    namespaced: true,
    state: {
        historyall: []
    },
    mutations: {
        SET_HISTORY(state, riwayat) {
            state.historyall = riwayat
        }
    },
    actions: {
        async riwayat({ commit }) {
            try {
                let response = await axios.get('/oasis/guardianship-history/npm=' + localStorage.getItem('userNpmTemp'), {
                   //
                })
                commit('SET_HISTORY', response.data.data.guardianship_history)
            } catch (error) {
                console.warn(error)
            }
        }
    },
    getters: {
        riwayat(state) {
            return state.historyall
        }
    }
}