import axios from "axios"
export default {
    namespaced: true,
    state: {
        matkulwajib: "",
        matkuldown: "",
        matkulup: ""
    },
    getters: {
        matkulwajib(state) {
            return state.matkulwajib
        },
        matkulup(state) {
            return state.matkulup
        },
        matkuldown(state) {
            return state.matkuldown
        }
    },
    mutations: {
        SET_MATKUL_WAJIB(state, data) {
            state.matkulwajib = data
        },
        SET_MATKUL_NGULANG(state, data) {
            state.matkuldown = data
        },
        SET_MATKUL_KEATAS(state, data) {
            state.matkulup = data
        },

    },
    actions: {
        async pilihMatkul({ commit }) {
            try {
                let response = await axios.get('/oasis/guardianship-course-list/npm=' + localStorage.getItem("userNpmTemp"), {
                   //
                })
                commit("SET_MATKUL_WAJIB", response.data.data.required_course)
                commit("SET_MATKUL_NGULANG", response.data.data.down_course)
                commit("SET_MATKUL_KEATAS", response.data.data.up_course)
            } catch (error) {
                console.warn(error)
            }
        }
    }
}