import axios from 'axios'
export default {
    namespaced: true,
    state: {
        info_perkuliahan: [],
        info_perkuliahan2: [],
        info_perkuliahan3: [],
        info_perkuliahan4: [],
        info_perkuliahan5: [],
        info_perkuliahan6: [],
        info_perkuliahan7: [],
        info_perkuliahan8: [],
    },
    mutations: {
        SET_1(state, perkuliahan) {
            state.info_perkuliahan = perkuliahan
        },
        SET_2(state, perkuliahan2) {
            state.info_perkuliahan2 = perkuliahan2
        },
        SET_3(state, perkuliahan3) {
            state.info_perkuliahan3 = perkuliahan3
        },
        SET_4(state, perkuliahan4) {
            state.info_perkuliahan4 = perkuliahan4
        },
        SET_5(state, perkuliahan5) {
            state.info_perkuliahan5 = perkuliahan5
        },
        SET_6(state, perkuliahan6) {
            state.info_perkuliahan6 = perkuliahan6
        },
        SET_7(state, perkuliahan7) {
            state.info_perkuliahan7 = perkuliahan7
        },
        SET_8(state, perkuliahan8) {
            state.info_perkuliahan8 = perkuliahan8
        },
    },
    actions: {
        async perkuliahan({ commit }) {
            try {

                let response = await axios.get('/elearning/schedule-history/username=' + localStorage.getItem('userNpmTemp'), {
                    //
                })
                // console.log(response.data.data[1].semester2.courses)
                commit('SET_1', response.data.data[0].semester1.courses)
                commit('SET_2', response.data.data[1].semester2.courses)
                commit('SET_3', response.data.data[2].semester3.courses)
                commit('SET_4', response.data.data[3].semester4.courses)
                commit('SET_5', response.data.data[4].semester5.courses)
                commit('SET_6', response.data.data[5].semester6.courses)
                commit('SET_7', response.data.data[6].semester7.courses)
                commit('SET_8', response.data.data[7].semester8.courses)
            } catch (error) {
                commit('SET_1', null)
                commit('SET_2', null)
                commit('SET_3', null)
                commit('SET_4', null)
                commit('SET_5', null)
                commit('SET_6', null)
                commit('SET_7', null)
                commit('SET_8', null)
            }
        }
    },
    getters: {
        mahasiswa(state) {
            return state.info_perkuliahan
        },
        mahasiswa2(state) {
            return state.info_perkuliahan2
        },
        mahasiswa3(state) {
            return state.info_perkuliahan3
        },
        mahasiswa4(state) {
            return state.info_perkuliahan4
        },
        mahasiswa5(state) {
            return state.info_perkuliahan5
        },
        mahasiswa6(state) {
            return state.info_perkuliahan6
        },
        mahasiswa7(state) {
            return state.info_perkuliahan7
        },
    }
}