import axios from "axios"
export default {
    namespaced: true,
    state: {
        cetak_khs: []
    },
    mutations: {
        SET_CETAK(state, khs) {
            state.cetak_khs = khs
        }
    },
    getters: {
        datacetak(state) {
            return state.cetak_khs
        }
    },
    actions: {
        async cetakkhs({ commit }) {
            try {
                let response = await axios.get('/siakad/khs-download/npm=' + localStorage.getItem('userNpmTemp'), {

                })
                // console.log(response.data.data.khs_data)
                commit('SET_CETAK', response.data.data)
            } catch (error) {
                console.warn(error)
            }
        }
    }

}