import axios from "axios"
export default {
    namespaced: true,
    state: {
        info_penghasilan: []
    },
    mutations: {
        SET_GAJI(state, salary) {
            state.info_penghasilan = salary
        }
    },
    actions: {
        async penghasilan({ commit }) {
            try {
                let response = await axios.get('/siakad/salary', {
                    //
                })
                // console.log(response.data.data)
                commit('SET_GAJI', response.data.data)
            } catch (error) {
                console.warn(error)
            }
        }
    },
    getters: {
        datapenghasilan(state) {
            return state.info_penghasilan
        }
    }
}