import axios from 'axios'
export default {
    namespaced: true,
    state: {
        status_kerja: []
    },
    mutations: {
        SET_KERJA(state, statusker) {
            state.status_kerja = statusker
        }
    },
    actions: {
        async stkerja({ commit }) {
            try {
                let response = await axios.get('/oasis/job/npm=' + localStorage.getItem('userNpmTemp'), {
                    //
                })
                // console.log(response.data.data)
                commit('SET_KERJA', response.data.data)
            } catch (error) {
                console.warn(error)
            }
        }
    },
    getters: {
        datastkerja(state) {
            return state.status_kerja
        }
    }
}