import axios from "axios";
export default {
  namespaced: true,
  state: {
    informasi: [],
  },
  mutations: {
    SET_NEWS(state, pengumuman) {
      state.informasi = pengumuman;
    },
  },
  actions: {
    async pengumuman({ commit }) {
      try {
        let response = await axios.get("/oasis/announcement", {
          
          success: function(){
            $("#datatables-responsive").dataTable({
              responsive: true
            });
          }
        }).then((response)=>{
            commit("SET_NEWS", response.data.data);
            
        })
        return response
        
      } catch (error) {
        console.warn(error);
      }
    },
  },
  getters: {
    pengumuman(state) {
      return state.informasi;
    },
  },
};
