router.beforeEach((to, from, next) => {
  if (to.matched.some(record => record.meta.requiresAuth)) {
    if (store.state.auth.token) {
      next()
      return
    }
    next('/login')
  } else {
    next()
  }
})
router.beforeEach((to,from, next) => {
  if (to.matched.some((record) => record.meta.guest)) {
    if (store.state.auth.token) {
      next("/home");
      return;
    }
    next();
  } else {
    next();
  }
})
router.beforeEach((to,from, next) => {
  if (to.matched.some((record) => record.meta.no_fill)) {
    if (store.getters['onepage/getPerwalian'].fillable_status) {
      next("/perwalian/perwalian");
      return;
    }
    next();
  } else {
    next();
  }
})
