import axios from 'axios'
export default {
    namespaced: true,
    state: {
        info_details: []
    },
    mutations: {
        SET_DETAIL(state, riwayat) {
            state.info_details = riwayat
        }
    },
    actions: {
        async detail({ commit }, guardianship_id) {
            try {
                let response = await axios.get('/oasis/guardianship-detail/npm=' + guardianship_id, {
                   //
                })
                commit('SET_DETAIL', response.data.data.detail)
                console.log(response.data.data.detail)
            } catch (error) {
                console.warn(error)
            }
        }

    },
    getters: {
        detail_riwayat(state) {
            return state.info_details
        }
    }
}