import Vue from 'vue'
import App from './App.vue'
import router from './router'
import jQuery from 'jquery'
window.$ = window.jQuery = jQuery
window.$ = require('jquery')
window.jQuery = require('jquery')
import 'popper.js'
import axios from 'axios'
// axios.defaults.withCredentials = true
axios.defaults.baseURL = 'https://oasisbackendapi.sttbandung.ac.id/api'
import 'bootstrap'
import 'bootstrap/dist/css/bootstrap.min.css'
import './assets/scss/light.scss'
import {Notyf} from 'notyf'
import 'notyf/notyf.min.css'
import store from './store'
const BTOK = 'Bearer ' + store.state.auth.token;
axios.defaults.headers.common['Authorization'] = BTOK
axios.defaults.headers.post['Authorization'] = BTOK
store.dispatch('auth/attempt', store.state.auth.token)
Vue.config.productionTip = true
axios.interceptors.response.use(undefined, function (error) {
  if (error) {
    const originalRequest = error.config;
    if (error.response.status === 401 && !originalRequest._retry) {
        originalRequest._retry = true;
        
        // store.dispatch('auth/signOut')
        // console.clear()
        
    }else if(error.response.status === 429 && !originalRequest._retry){
      store.dispatch('auth/attempt', store.state.auth.token)
    }
  }
})
import moment from 'moment';

Vue.filter('formatDate', function(value) {
    if (value) {
        return moment(String(value)).format('DD/MM/YYYY hh:mm')
    }
});
// axios.defaults.withCredentials = true
new Vue({
  router,
  store,
  mode: 'history',
  mounted(){
   
  },
  render: h => h(App),
  provide:()=>{
    return{
      notyf: new Notyf({
        duration: 5000,
        position: {
          x: 'right',
          y: 'top',
        }
      }),

    }
  }
}).$mount('#app')

