<script>
import { mapGetters, mapActions } from "vuex";
export default {
  name: "Header",
  computed: {
    ...mapGetters({
      users: "auth/users",
      authenticated: "auth/authenticated",
      getPerwalian: "onepage/getPerwalian",
    }),
  },
  methods: {
    ...mapActions({
      sigOutAct: "auth/signOut",
      cekPerwalian: "onepage/perwalian",
    }),

    signOut() {
      this.sigOutAct().then(() => {
        this.$router.replace({
          name: "Login",
        });
      });
    },
    setPerwalian() {
      this.cekPerwalian();
    },
  },
  created() {
    if (this.authenticated) {
      this.setPerwalian();
      const statusPerwalian = this.getPerwalian.fillable_status;
      if (statusPerwalian == false) {
        setTimeout(() => {
          $("#myModal").modal({
            backdrop: "static",
            keyboard: false,
            show: true,
          });
        }, 3000);
      }
    }
  },
};
</script>

