import axios from "axios"
export default {
    namespaced: true,
    state: {
        jadwal_kuliah: []
    },
    mutations: {
        SET_JADWAL(state, kuliah) {
            state.jadwal_kuliah = kuliah
        }
    },
    actions: {
        async kuliah({ commit }) {
            try {
                let response = await axios.get('/siakad/get-class-schedule/npm=' + localStorage.getItem('userNpmTemp') + '&day=now', {
                   //
                })

                commit('SET_JADWAL', response.data.data.list)
            } catch (error) {
                commit('SET_JADWAL', "")
            }
        }
    },
    getters: {
        kuliah(state) {
            return state.jadwal_kuliah
        }
    }
}